@import "../../frontend-shell/shared/variables";

.nav-menu {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;

  &--wrap {
    background-color: white;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.1);
    position: relative;
  }

  &__item {
    font-size: 18px;
    padding: 28px 0 28px;

    > a {
      // TODO: удалить после подключения шрифта
      letter-spacing: -1px;
    }

    &:hover {
      > a {
        color: $accent-color;
        text-decoration: none;
        &::before {
          opacity: 1;
        }
      }
    }

    a {
      text-transform: uppercase;
      color: black;
      transition: color $transition-time ease-out;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        bottom: -10px;
        left: 0;
        width: 100%;
        height: 3px;
        opacity: 0;
        transition: opacity $transition-time ease-out;
        background-color: $accent-color;
      }
      &:focus {
        color: $accent-dark-color;
        &::before {
          background-color: $accent-dark-color;
        }
      }
      &:hover,
      &:focus {
        text-decoration: none;
      }
    }

    &:hover .nav-menu__dropdown-container {
      z-index: 5;
      opacity: 1;
      transition: opacity $transition-time ease-out;
    }
  }

  &__dropdown-container {
    position: absolute;
    width: 100%;
    left: 0;
    right: 0;
    top: 100%;
    background-color: $text-color;
    opacity: 0;
    z-index: -1;
    transition: opacity $transition-time ease-out;

    &.easing {
      z-index: 5;
    }

    a {
      color: white;
      font-size: 20px;
    }
  }
  &__dropdown-wrap {
    display: flex;
  }

  &__dropdown-list {
    display: flex;
    flex-flow: column wrap;
    max-height: 192px;
    padding: 30px 0;
    list-style: none;
  }

  &__dropdown-item {
    margin-bottom: 30px;
    margin-right: 90px;

    &:hover{
      text-decoration: underline;
    }
  }
}